.burger {
  position: relative;
  border: none;
  padding: 0;
  width: 20px;
  height: 11px;
  color: #fff;
  background-color: transparent;
  cursor: pointer;
  margin-left: auto;
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  right: 0;
  width: 100%;
  height: 1px;
  border-radius: 20px;
  background-color: currentColor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger::before {
  top: 0;
  width: 50%;
}
.burger::after {
  top: calc(100% - 1px);
  width: 75%;
}
.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  border-radius: 20px;
  height: 1px;
  background-color: #fff;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}
.burger--active::before {
  top: 50%;
  z-index: 20;
  width: 100%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active::after {
  top: 50%;
  z-index: 20;
  width: 100%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}